/* Default custom colors */

:root {
    --color-theme-l5: #F2F9FE;
    --color-theme-l4: #D2EAFD;
    --color-theme-l3: #A6D4FA;
    --color-theme-l2: #79BFF8;
    --color-theme-l1: #4DAAF6;
    --color-theme-c0: #2196F3;
    --color-theme-d1: #0C87EB;
    --color-theme-d2: #0B78D1;
    --color-theme-d3: #0A69B7;
    --color-theme-d4: #085A9D;
    --color-theme-d5: #074B83;
}

/* End Default custom colors */

/* Media queries */

