/* General */

body {
    background-color: #F5F5F5;
}

a {
    color: var(--color-theme-d1);
}

a:hover,
a:focus {
    color: var(--color-theme-d2);
}

/* End General */

/* Sidebar */

#sidebar {
    overflow-y: auto;
    width: 250px;
    height: 100%;
    margin-left: -210px;
    float: left;
    z-index: 99999;
    margin-bottom: 0;
}

#sidebar:hover {
    margin-left: 0;
}

#sidebar::-webkit-scrollbar {
    width: 0;
}

@media (max-width: 991px) {
    #sidebar:hover::-webkit-scrollbar {
        width: 5px;
    }
}

@media (min-width: 992px) {
    #sidebar::-webkit-scrollbar {
        width: 8px;
    }
}

#sidebar::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.1);
}

#sidebar::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.3);
}

#sidebar.navbar-inverse {
    border: none;
    background: var(--color-theme-d2);
}

#sidebar .navbar-nav a:hover,
#sidebar .navbar-nav a:focus,
#sidebar .navbar-nav .active a,
#sidebar .navbar-nav .active a:hover,
#sidebar .navbar-nav .active a:focus {
    color: #FFFFFF;
    background: var(--color-theme-c0);
    box-shadow: none;
}

#sidebar .navbar-toggle:hover,
#sidebar .navbar-toggle:focus {
    background: red;
}

#sidebar.navbar {
    border-radius: 0;
}

#sidebar .navbar-brand {
    color: #FFFFFF;
}

#sidebar .navbar-nav > li > a {
    color: #CCCCCC;
    text-shadow: none;
}

#sidebar .navbar-nav > li > a:hover,
#sidebar .navbar-nav > li > a:focus {
    background-color: var(--color-theme-d1);
}

.navbar > .container-fluid .navbar-brand {
    margin-left: 0;
}

#sidebar,
.main {
    transition: all 200ms ease-in-out;
}

#sidebar .navbar-brand,
#sidebar .navbar-header {
    width: 100%;
    text-align: center;
}

#sidebar .navbar-header {
    background-color: var(--color-theme-d3);
}

#sidebar .navbar-header {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
    margin: 0;
}

#sidebar .navbar-header img {
    margin-top: 10px;
    height: 75px;
    width: 75px;
}

@media (max-width: 991px) {
    #sidebar .navbar-header img {
        height: 30px;
        width: 30px;
        position: absolute;
        right: 6px;
    }
}

#sidebar .navbar-nav .open .dropdown-menu {
    position: static;
    width: 100%;
    background-color: transparent;
    padding: 0;
}

#sidebar .navbar-collapse, #sidebar .container-fluid {
    padding: 0;
}

#sidebar .navbar-nav .dropdown.open .dropdown-menu > li > a {
    color: #CCCCCC;
}

#sidebar .navbar-nav .dropdown.open .dropdown-menu > li > a:hover,
#sidebar .navbar-nav .dropdown.open .dropdown-menu > li > a:focus {
    color: #CCCCCC;
    background: var(--color-theme-d1);
}

#sidebar .navbar-nav .dropdown.open a {
    background: var(--color-theme-d3);
}

#sidebar li {
    width: 100%;
}

#sidebar li a {
    padding-top: 20px;
    padding-bottom: 20px;
}

@media (min-width: 992px) {
    #sidebar {
        margin-left: 0;
    }
}

.navbar-nav {
    margin: 0;
}

.navbar-nav span.glyphicon {
    font-size: 16px;
    float: right !important;
}

.navbar-brand {
    padding-top: 5px;
}

h1.navbar-brand {
    font-size: 24px;
}

/* End Sidebar */

/* Login Sidebar */

[login] #sidebar {
    outline: none;
    margin-left: -250px;
}

[login] #sidebar:hover,
[login] #sidebar:focus {
    margin-left: 0;
}

@media (min-width: 992px) {
    [login] #sidebar {
        margin-left: 0;
    }
}

/* End Login Sidebar */

/* Header */

.header {
    position: fixed;
    padding: 20px 50px;
    width: calc(100% - 40px);
    z-index: 99998;
    background-color: var(--color-theme-d4);
    color: #FFFFFF;
    font-size: 18px;
    right: 0;
}

.header {
    transition: all 200ms ease-in-out;
}

@media (min-width: 992px) {
    .header {
        width: calc(100% - 250px);
    }
}

.header a,
.header a:hover,
.header a:focus {
    margin-left: 25px;
    text-decoration: none;
    color: #FFFFFF;
    float: right;
    cursor: pointer;
}

/* End Header */

/* Login Header */

[login] .header {
    width: 100%;
}

@media (min-width: 992px) {
    [login] .header {
        width: calc(100% - 250px);
    }
}

/* End Login Header */

/* Main */

.main {
    padding: 10px 10px 0 10px;
    position: absolute;
    height: calc(100% - 65px);
    width: calc(100% - 40px);
    margin-top: 65px;
    margin-left: 40px;
    float: right;
    overflow-y: scroll;
}

@media (min-width: 992px) {
    .main {
        width: calc(100% - 250px);
        margin-left: 250px;
    }
}

/* End Main */

/* Login Main */

[login] .main {
    width: 100%;
    margin-left: 0;
}

@media (min-width: 992px) {
    [login] .main {
        width: calc(100% - 250px);
        margin-left: 250px;
    }
}

/* End Login Main */

/* Form */

.form-group {
    margin-top: 40px;
}

.form-control option:hover {
    background-color: red;
}

#frm-signup .avatar {
    background-repeat: no-repeat;
    height: 150px;
    width: 150px;
    margin-top: 25px;
    margin-bottom: 25px;
}

#frm-signup h1 {
    text-align: center;
}

.btn-file input[type=file] {
    cursor: pointer;
}

.well {
    min-height: calc(100% - 20px);
    border-radius: 0;
    background: #FFFFFF;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
}

.form-control {
    padding-left: 0;
    padding-right: 0;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #9E9E9E;
    box-shadow: none;
    transition: all .2s ease-in-out;
}

.form-control:focus {
    border-bottom: 1px solid var(--color-theme-d3);
    box-shadow: 0 1px 0 0 var(--color-theme-d3);
}

label.control-label {
    position: absolute;
    color: #AAAAAA;
    cursor: text;
    top: .8rem;
    transition: all .25s ease-in-out;
}

#frm-signup .form-control::-webkit-input-placeholder {
    opacity: 0;
}

:not(:placeholder-shown) + label.control-label,
:focus + label.control-label {
    font-size: 12px;
    color: var(--color-theme-d3);
    top: -1rem;
    cursor: default;
}

select + label.control-label {
    top: -1rem;
}

select {
    padding-left: 0;
    padding-right: 0;
}

.avatar .glyphicon {
    cursor: pointer;
    position: relative;
    top: 115px;
    font-size: 20px;
    background-color: var(--color-theme-d3);
    border-radius: 50%;
    padding: 10px;
    color: #fff;
}

.avatar .glyphicon-camera {
    left: 115px;
}

.avatar .glyphicon-remove {
    right: 50px;
}

/* End Form */

/* Form Login */

#frm-login {
    margin-top: 150px;
    padding: 0 15px;
}

#frm-login .form-control {
    border: none;
}

#frm-login .input-group {
    margin-top: 25px;
}

#frm-login [type="text"].form-control {
    padding-right: 10px;
}

#frm-login .input-group-addon {
    border-radius: 0;
    border: none;
    background-color: #FFFFFF;
    color: var(--color-theme-d2);
}

#frm-login [type="submit"].form-control {
    margin-top: 25px;
    background-color: var(--color-theme-d5);
    color: #FFFFFF;
    opacity: 0.9;
}

#frm-login [type="submit"].form-control:hover,
#frm-login [type="submit"].form-control:hover {
    opacity: 1;
}

#frm-login > label {
    display: block;
}

#frm-login > a {
    margin-bottom: 25px;
}

/* End Form Login */

/* Tooltip */

.tooltip.top .tooltip-arrow {
    border-top-color: var(--color-theme-d2);
}

.tooltip-inner {
    background-color: var(--color-theme-d2);
}

/* End Tooltip */

/* Table */

.table-striped > tbody > tr:nth-of-type(odd) {
    background-color: #EEEEEE;
}

.table-hover > tbody > tr:hover {
    background-color: #dddddd;
}

.table-hover td,
.table-hover th {
    padding: 3px 0;
}

/* End Table */

/* Panel */

.panel-group .panel + .panel {
    margin-top: 0;
}

.panel-heading,
.panel-group .panel {
    border-radius: 0;
}

.panel-primary {
    border-color: var(--color-theme-d1);
}

.panel-primary > .panel-heading {
    color: #fff;
    background: var(--color-theme-d1);
    border-color: var(--color-theme-d1);
}

.panel-primary > .panel-heading + .panel-collapse > .panel-body {
    border-top-color: var(--color-theme-d1);
}

.panel-primary > .panel-footer + .panel-collapse > .panel-body {
    border-bottom-color: var(--color-theme-d1);
}

.panel.noborder {
    border: none;
    box-shadow: none;
}

.panel-noborder {
    border: none;
    box-shadow: none;
}

/* End Panel */

/* Panel tabs */

.nav-pills > li.active > a, .nav-pills > li.active > a:hover, .nav-pills > li.active > a:focus {
    background-color: var(--color-theme-d2);
}

.panel-tabs {
    position: relative;
    bottom: 30px;
    clear: both;
    border-bottom: 1px solid transparent;
}

.panel-tabs > li {
    float: left;
    margin-bottom: -1px;
}

.panel-tabs > li > a {
    margin-right: 2px;
    margin-top: 4px;
    line-height: .85;
    border: 1px solid transparent;
    border-radius: 4px 4px 0 0;
    color: #ffffff;
}

.panel-tabs > li > a:hover {
    border-color: transparent;
    color: #ffffff;
    background-color: transparent;
}

.panel-tabs > li.active > a,
.panel-tabs > li.active > a:hover,
.panel-tabs > li.active > a:focus {
    color: #fff;
    cursor: default;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    background-color: rgba(255, 255, 255, .23);
    border-bottom-color: transparent;
}

.nav-tabs-dropdown {
    display: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    color: var(--color-theme-d3);
}

.nav-tabs-dropdown:before {
    content: "\e114";
    font-family: 'Glyphicons Halflings', "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: absolute;
    right: 30px;
    color: var(--color-theme-d3);
}

@media (min-width: 768px) {
    #nav-tabs-wrapper {
        display: block !important;
    }
}

@media (max-width: 767px) {
    .nav-tabs-dropdown {
        display: block;
    }

    #nav-tabs-wrapper {
        display: none;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        text-align: center;
    }

    .nav-tabs-horizontal {
        min-height: 20px;
        padding: 19px;
        margin-bottom: 20px;
        background-color: #f5f5f5;
        border: 1px solid #e3e3e3;
        border-radius: 4px;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
    }

    .nav-tabs-horizontal > li {
        float: none;
    }

    .nav-tabs-horizontal > li + li {
        margin-left: 2px;
    }

    .nav-tabs-horizontal > li,
    .nav-tabs-horizontal > li > a {
        background: transparent;
        width: 100%;
    }

    .nav-tabs-horizontal > li > a {
        border-radius: 4px;
    }

    .nav-tabs-horizontal > li.active > a,
    .nav-tabs-horizontal > li.active > a:hover,
    .nav-tabs-horizontal > li.active > a:focus {
        color: #ffffff;
        background-color: #428bca;
    }
}

/* End Panel tabs */

/* Carousel */

#custom_carousel .item {
    color: #000;
    background-color: white;
    padding: 20px 0;
}

#custom_carousel .controls {
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0;
    margin: 0;
    white-space: nowrap;
    text-align: center;
    position: relative;
    background: #ddd;
}

#custom_carousel .controls li {
    display: table-cell;
    width: 1%;
    max-width: 90px
}

#custom_carousel .controls li.active {
    background-color: #eee;
    border-top: 3px solid #428bca;
}

#custom_carousel .controls a small {
    overflow: hidden;
    display: block;
    font-size: 10px;
    margin-top: 5px;
    font-weight: bold
}

