/* Feature card */

.feature-card {
    width: 90%;
    height: auto;
    text-align: center;
    margin-top: 3em;
    -moz-transition: background-color 2s ease-out;
    -o-transition: background-color 2s ease-out;
    transition: background-color 2s ease-out;
    -webkit-transition: 0.2s ease;
}

.feature-card:hover {
    width: 90%;
    height: auto;
    text-align: center;
    -ms-transform: scale(1.1, 1.1); /* IE 9 */
    -webkit-transform: scale(1.1, 1.1); /* Safari */
    transform: scale(1.1, 1.1);
}

.feature-img {
    width: 30%;
    height: 30%;
    border-radius: 100px;
}

.not-rounded {
    border-radius: 0;
}

.feature-title {
    font-weight: bold;
    display: inline;
    margin-left: 1em;
    color: rgb(64, 111, 173);
}

.feature-text {
    font-weight: bold;
}

.feature-link:link {
    text-decoration: none;
}

.feature-link:visited {
    text-decoration: none;
}

.feature-link:hover {
    text-decoration: none;
}

.feature-link:active {
    text-decoration: none;
}

@media (max-width: 991px) {
    .feature-card {
        margin-top: 2em;
    }
}

/* End Feature card */

